.project-section {
    display: flex;
    margin: 0 100px 0 100px;
    justify-content: center;
    align-items: center;
    gap: 80px;
    font-family: Montserrat;
    font-style: normal;
    line-height: normal;
}

.picture-right {
    flex-direction: row-reverse;
}

.project-img {
    height: 50%;
}

.project-header {
    font-size: 49.245px;
    font-weight: 700;
    font-family: Noticia Text;
    margin-bottom: 2rem;
}

.project-description {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 2rem;
}

.project-tech {
    font-size: 18.467px;
    margin-bottom: 2rem;
    font-weight: 500;
}

.button-container {
    display: flex;
    gap: 30px;
}

@media screen and (max-width: 1410px) {
    .project-img {
        height: 32vw;
    }
}

@media screen and (max-width: 900px) {
    .project-section {
        flex-direction: column;
    }
    
    .project-img {
        height: 32vw;
    }
}