#experience {
    padding: 50px 100px;
}

#experienceHeader {
    margin: 0;
}

.hide {
    display: none;
}

.education {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px;
    border-top: 1px solid white;
    margin-top: 50px;
}

.tech-experience, .other-experience {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
}

@media screen and (max-width: 700px) {
    #experience {
        padding: 50px 15px;
    }   
}